elect--disabled .multiselect__select{background:transparent}.multiselect__spinner:after,.multiselect__spinner:before{background:transparent;border:3px solid transparent;border-radius:100%;border-top-color:orange;box-shadow:0 0 0 1px transparent;content:\"\";height:16px;left:50%;margin:-8px 0 0 -8px;position:absolute;top:50%;width:16px}.multiselect__spinner{border-radius:50px}.qty .count{color:#0f213d!important;font-family:Open Sans;font-size:25px;font-weight:700;line-height:30px;min-width:35px;padding:0 2px}.qty .count,.qty button{display:inline-block;text-align:center;vertical-align:top}.qty button{background:#f08b23;border-radius:50%;color:#fff;cursor:pointer;font:30px/1 Arial,sans-serif;height:30px;padding:0;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;width:30px}.qty div{text-align:center}.qty .minus:hover,.qty .plus:hover{background:#0f213d!important;color:#fff}.qty input{border:0;width:60%}.qty input::-webkit-inner-spin-button,.qty input::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}.qty input:disabled{background-color:#fff}", "",{"version":3,"sources":["webpack://./resources/js/desktop/mobile/pages/AviaModule/Avia.vue"],"names":[],"mappings":"AAkuBA,4CACA,yCAEA,WAAA,CADA,oBAEA,CACA,wBACA,oBACA,CACA,WACA,QAAA,CACA,SACA,CACA,CACA,oCACA,WACA,wBACA,CACA,eACA,oBACA,CACA,CACA,oCACA,mBACA,qBACA,CAEA,CACA,0BAIA,eAAA,CAEA,qBAAA,CAHA,sBAAA,CAFA,iBAAA,CAIA,WAAA,CAHA,mBAKA,CACA,sBAGA,iCAAA,CAKA,kBAAA,CAJA,uBAAA,CAOA,+BAAA,CANA,cAAA,CAOA,eAAA,CAFA,qBAAA,CAJA,gBAAA,CAGA,eAAA,CAFA,YAAA,CALA,WAAA,CAWA,UACA,CACA,wCACA,uBACA,CAFA,mCACA,uBACA,CACA,sCAEA,aAAA,CACA,+BAAA,CACA,yBACA,CACA,8BAKA,aAAA,CACA,cAAA,CAJA,iBAAA,CACA,UAAA,CACA,SAGA,CACA,gCAIA,WAAA,CACA,gBAIA,CAEA,+BASA,+BAAA,CALA,aAAA,CAEA,cAAA,CALA,cAAA,CACA,eAAA,CAQA,qBAAA,CAPA,gBAAA,CAEA,iBAEA,CAKA,gCAQA,4BAAA,CAPA,oBAAA,CAGA,qBAAA,CACA,0BAAA,CAHA,aAAA,CAIA,iBAAA,CACA,qBAAA,CAJA,yBAMA,CACA,kNAGA,iCACA,CAKA,wFACA,8BAAA,CACA,qBACA,CAKA,iHACA,iCACA,CAOA,0JACA,oCACA,CACA,8CACA,iCACA,CACA,0DACA,+BAAA,CACA,yBACA,CACA,4BACA,uBACA,CACA,8BACA,sBACA,CACA,sEACA,uBAAA,CACA,+BAAA,CACA,yBACA,CACA,4BAMA,gbAAA,CAEA,uBAAA,CADA,2BAAA,CANA,UAAA,CACA,oBAAA,CAGA,WAAA,CAFA,eAAA,CACA,UAKA,CACA,0BACA,aAAA,CACA,+BAAA,CACA,yBACA,CAEA,gCAEA,yBAAA,CADA,sBAEA,CAEA,YACA,uBAAA,CACA,+BAAA,CACA,eACA,CACA,mBACA,wBAAA,CACA,kBAAA,CACA,cAAA,CACA,eACA,CACA,kBACA,kBACA,CACA,6BACA,UACA,CAOA,gIACA,kBACA,CACA,aAEA,+BAAA,CACA,eACA,CACA,uCAJA,aAMA,CACA,qDACA,oBACA,CACA,sCACA,uBAAA,CACA,+BAAA,CACA,eACA,CAJA,iCACA,uBAAA,CACA,+BAAA,CACA,eACA,CACA,uBACA,eACA,CACA,4CACA,sBACA,CACA,yDAaA,sBAAA,CAFA,4BAAA,CAHA,kBAAA,CAGA,uBAAA,CACA,gCAAA,CAVA,UAAA,CAKA,WAAA,CAHA,QAAA,CACA,oBAAA,CAJA,iBAAA,CAEA,OAAA,CAGA,UAQA,CACA,sBACA,kBACA,CAEA,YAUA,uBAAA,CADA,qBAAA,CANA,cAAA,CACA,eAAA,CACA,gBAAA,CAEA,cAAA,CADA,aAKA,CACA,wBAXA,oBAAA,CAOA,iBAAA,CANA,kBAyBA,CAfA,YAcA,kBAAA,CAFA,iBAAA,CALA,UAAA,CAHA,cAAA,CAMA,4BAAA,CADA,WAAA,CAIA,SAAA,CAZA,wBAAA,CACA,qBAAA,CACA,oBAAA,CAKA,UAOA,CACA,SACA,iBACA,CAKA,mCACA,4BAAA,CACA,UACA,CAEA,WACA,QAAA,CACA,SACA,CACA,4EAEA,uBAAA,CACA,QACA,CACA,oBACA,qBACA","sourcesContent":["<template>\n    <div id=\"avia-module\">\n        <div class=\"m-auto row align-items-center justify-content-center\">\n            <div v-if=\"!aviaModule.is_multi_city\" class=\"row mx-auto align-items-center justify-content-center mt-2\" style=\"width: 100%;\">\n                <div class=\"col-12 col-md-6 mb-2 mt-2\">\n                    <h6 class=\"avia-title\">Город вылета</h6>\n                    <multiselect\n                        v-model=\"selectedOriginCity\"\n                        label=\"name\"\n                        track-by=\"code\"\n                        placeholder=\"Город вылета\"\n                        :options=\"options\"\n                        :multiple=\"false\"\n                        :searchable=\"true\"\n                        :loading=\"isAviaCitiesLoading\"\n                        :internal-search=\"false\"\n                        :clear-on-select=\"false\"\n                        :close-on-select=\"true\"\n                        :options-limit=\"300\"\n                        :max-height=\"300\"\n                        :show-no-results=\"false\"\n                        :hide-selected=\"false\"\n                        :show-labels=\"false\"\n                        @search-change=\"getAviaCities\"\n                        @input=\"changeCityFrom\"\n                    >\n                        <template slot=\"singleLabel\" v-if=\"options.length>0 || selectedOriginCity!=null\" slot-scope=\"props\">\n                            <div v-if=\"!props.option.city_name\">\n                         <span class=\"option__desc\">\n                            <span class=\"option__title\">{{ props.option.name }}, {{ props.option.country_name }}</span>\n                         </span>\n                                <span class=\"option__desc float-right\">{{props.option.code}}</span>\n                            </div>\n\n                            <div v-if=\"props.option.city_name\">\n                         <span class=\"option__desc\">\n                             <span class=\"option__title\">{{ props.option.city_name }}, {{ props.option.country_name }}</span>\n                         </span>\n                                <span class=\"option__desc float-right\">{{props.option.code}}</span>\n                            </div>\n                        </template>\n                        <template slot=\"option\" slot-scope=\"props\">\n                            <div v-if=\"!props.option.city_name\">\n                                    <span class=\"option__desc\">\n                                        <span class=\"option__title\">{{ props.option.name }}</span>\n                                    </span>\n                                <div class=\"row align-items-center option__desc\" style=\"width: 100%; height: 100%\">\n                                    <div class=\"col-6\">\n                                        <span class=\"option__desc\">Все аэропорты</span>\n                                    </div>\n                                    <div class=\"col-6\">\n                                        <span class=\"option__desc float-right\">{{props.option.code}}</span>\n                                    </div>\n                                </div>\n                            </div>\n\n                            <div v-if=\"props.option.city_name\">\n                         <span class=\"option__desc\">\n                            <span class=\"option__title\">{{ props.option.city_name }}</span>\n                        </span>\n                                <div class=\"row align-items-center\" style=\"width: 100%; height: 100%\">\n                                    <div class=\"col-6\">\n                                        <span class=\"option__desc\">{{ props.option.name }}</span>\n                                    </div>\n                                    <div class=\"col-6\">\n                                        <span class=\"option__desc float-right\">{{props.option.code}}</span>\n                                    </div>\n                                </div>\n                            </div>\n                        </template>\n                        <span slot=\"noResult\">Упс! Ничего не найдено. Рассмотрите возможность изменения поискового запроса..</span>\n                        <span slot=\"noOptions\">Начните печатать название города...</span>\n                    </multiselect>\n                </div>\n                <div class=\"col-12 col-md-6 mb-2 mt-2\">\n                    <h6 class=\"avia-title\">Город прибытия</h6>\n                    <multiselect\n                        v-model=\"selectedDestinationCity\"\n                        label=\"name\"\n                        track-by=\"code\"\n                        placeholder=\"Город вылета\"\n                        :options=\"options\"\n                        :multiple=\"false\"\n                        :searchable=\"true\"\n                        :loading=\"isAviaCitiesLoading\"\n                        :internal-search=\"false\"\n                        :clear-on-select=\"false\"\n                        :close-on-select=\"true\"\n                        :options-limit=\"300\"\n                        :max-height=\"300\"\n                        :show-no-results=\"false\"\n                        :hide-selected=\"false\"\n                        :show-labels=\"false\"\n                        @search-change=\"getAviaCities\"\n                        @input=\"changeCityTo\"\n                    >\n                        <template slot=\"singleLabel\" v-if=\"options.length>0 || selectedDestinationCity!=null\" slot-scope=\"props\">\n                            <div v-if=\"!props.option.city_name\">\n                                             <span class=\"option__desc\">\n                                                <span class=\"option__title\">{{ props.option.name }}, {{ props.option.country_name }}</span>\n                                             </span>\n                                <span class=\"option__desc float-right\">{{props.option.code}}</span>\n                            </div>\n\n                            <div v-if=\"props.option.city_name\">\n                                             <span class=\"option__desc\">\n                                                 <span class=\"option__title\">{{ props.option.city_name }}, {{ props.option.country_name }}</span>\n                                             </span>\n                                <span class=\"option__desc float-right\">{{props.option.code}}</span>\n                            </div>\n                        </template>\n                        <template slot=\"option\" slot-scope=\"props\">\n                            <div v-if=\"!props.option.city_name\">\n                                             <span class=\"option__desc\">\n                                                <span class=\"option__title\">{{ props.option.name }}</span>\n                                             </span>\n                                <div class=\"row align-items-center option__desc\" style=\"width: 100%; height: 100%\">\n                                    <div class=\"col-6\">\n                                        <span class=\"option__desc\">Все аэропорты</span>\n                                    </div>\n                                    <div class=\"col-6\">\n                                        <span class=\"option__desc float-right\">{{props.option.code}}</span>\n                                    </div>\n                                </div>\n                            </div>\n\n                            <div v-if=\"props.option.city_name\">\n                                             <span class=\"option__desc\">\n                                                <span class=\"option__title\">{{ props.option.city_name }}</span>\n                                            </span>\n                                <div class=\"row align-items-center\" style=\"width: 100%; height: 100%\">\n                                    <div class=\"col-6\">\n                                        <span class=\"option__desc\">{{ props.option.name }}</span>\n                                    </div>\n                                    <div class=\"col-6\">\n                                        <span class=\"option__desc float-right\">{{props.option.code}}</span>\n                                    </div>\n                                </div>\n                            </div>\n                        </template>\n                        <span slot=\"noResult\">Упс! Ничего не найдено. Рассмотрите возможность изменения поискового запроса..</span>\n                        <span slot=\"noOptions\">Начните печатать название города...</span>\n                    </multiselect>\n                </div>\n                <div class=\"col-12 col-md-4\">\n                    <h6 class=\"avia-title\">Туда</h6>\n                    <datepicker v-model=\"depart_date\" name=\"depart_date\" @input=\"changeDepartDate\" :language=\"languages['ru']\" :disabled-dates=\"disabledDates1\"></datepicker>\n                </div>\n                <div class=\"col-12 col-md-4\">\n                    <h6 class=\"avia-title\">Обратно</h6>\n                    <datepicker v-model=\"return_date\" name=\"return_date\" @input=\"changeReturnDate\" :clear-button=\"true\" :language=\"languages['ru']\" :disabled-dates=\"disabledDates2\"></datepicker>\n                </div>\n                <div class=\"col-12 col-md-4\">\n                    <h6 class=\"avia-title\">Класс</h6>\n                    <div class=\"row align-items-center justify-content-center m-auto\" style=\"width: 100%;height: 100%;\">\n                        <multiselect\n                            v-model=\"avia_class\"\n                            :options=\"classes\"\n                            :option-height=\"25\"\n                            placeholder=\"Класс\"\n                            :show-labels=\"false\"\n                            :maxHeight=\"200\"\n                            :allow-empty=\"false\"\n                            :searchable=\"false\"\n                            @input=\"changeClass\"\n                        >\n                        </multiselect>\n                    </div>\n                </div>\n                <div class=\"col-12 col-md-4\">\n                    <div class=\"row align-items-center justify-content-center mx-auto mt-3\" style=\"width: 100%;height: 100%;\">\n                        <h6 class=\"avia-title\">Взрослые</h6>\n                        <div class=\"row qty align-items-center justify-content-center m-auto\" style=\"width: 100%;height: 100%;\">\n                            <button type=\"button\" class=\"btn minus text-center \" :disabled=\"adults===1\"\n                                    @click=\"decrementAdults()\">-\n                            </button>\n                            <input type=\"number\" class=\"count\" name=\"qty\" v-model=\"adults\" step=\"1\" min=\"1\" @change=\"changeAdults\">\n                            <button type=\"button\" class=\"btn plus text-center\"\n                                    @click=\"incrementAdults()\">+\n                            </button>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"col-md-4\">\n                    <div class=\"row align-items-center justify-content-center mx-auto mt-3\" style=\"width: 100%;height: 100%;\">\n                        <h6 class=\"avia-title\">Дети до 12 лет</h6>\n                        <div class=\"row qty align-items-center justify-content-center m-auto\" style=\"width: 100%;height: 100%;\">\n                            <button type=\"button\" class=\"btn minus text-center \" :disabled=\"children_under_12===0\"\n                                    @click=\"decrementChildrenUnder12()\">-\n                            </button>\n                            <input type=\"number\" class=\"count\" name=\"qty\" v-model=\"children_under_12\" step=\"1\" min=\"0\" @change=\"changeChildrenUnder12\">\n                            <button type=\"button\" class=\"btn plus text-center\"\n                                    @click=\"incrementChildrenUnder12()\">+\n                            </button>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"col-md-4\">\n                    <div class=\"row align-items-center justify-content-center mx-auto mt-3\" style=\"width: 100%;height: 100%;\">\n                        <h6 class=\"avia-title\">Дети до 2 лет</h6>\n                        <div class=\"row qty align-items-center justify-content-center m-auto\" style=\"width: 100%;height: 100%;\">\n                            <button type=\"button\" class=\"btn minus text-center \" :disabled=\"children_under_2===0\"\n                                    @click=\"decrementChildrenUnder2()\">-\n                            </button>\n                            <input type=\"number\" class=\"count\" name=\"qty\" v-model=\"children_under_2\" step=\"1\" min=\"0\" @change=\"changeChildrenUnder2\">\n                            <button type=\"button\" class=\"btn plus text-center\"\n                                    @click=\"incrementChildrenUnder2()\">+\n                            </button>\n                        </div>\n                    </div>\n                </div>\n\n                <!--        откуда - куда-->\n\n                <!--        пассажиры, дети до 12, дети до 2 лет-->\n                <!--        класс: эконом и бизнес-класс\n                 дата туда и обратно\n                 -->\n                <!--        сложный маршрут\n                город вылета город прибытия\n                дата вылета\n                пассажиры\n                -->\n\n            </div>\n            <div v-if=\"aviaModule.is_multi_city\" class=\"row mx-auto mt-5 align-items-center justify-content-center\" style=\" width: 100%;\">\n\n            </div>\n            <div class=\"appBottomMenu\">\n                <div class=\"item ps-4 pe-2 ml-0 mr-auto\">\n                    <div class=\"col\">\n                        <div class=\"row\">\n                            <div class=\"ell active mr-1\"></div>\n                            <div class=\"ell mr-1\"></div>\n                        </div>\n                    </div>\n                </div>\n                <button class=\"btn item mr-0 ml-auto ps-2 pe-2\">\n                    <div class=\"col\">\n                        <div class=\"action-button\" @click=\"nextStep\">\n                            <div class=\"icon icon-keyboard_arrow_right\"></div>\n                        </div>\n                    </div>\n                </button>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\n    import * as lang from \"vuejs-datepicker/src/locale\";\n    export default {\n        name: \"Avia\",\n        data () {\n            return {\n                step1: true,\n                step2: false,\n                step3: false,\n                selectedOriginCity:'',\n                selectedDestinationCity:'',\n                options: [],\n                depart_date: new Date(),\n                return_date: null,\n                adults: 1,\n                children_under_12: 0,\n                children_under_2: 0,\n                avia_class:'Эконом класс',\n                classes: ['Эконом класс' , 'Бизнес класс'],\n                languages: lang,\n                disabledDates1: {\n                    to: new Date(new Date().setDate(new Date().getDate() - 1)),\n                },\n                disabledDates2: {\n                    to: new Date(new Date().setDate(new Date().getDate() - 1)),\n                },\n                disabled:true\n            }\n        },\n        mounted() {\n            if(this.avia_steps.step1 == false)\n            {\n                this.$router.push('/avia-module/')\n            }\n            this.adults = this.aviaModule.adults;\n            this.children_under_12 = this.aviaModule.children_under_12;\n            this.children_under_2 = this.aviaModule.children_under_2;\n            this.selectedOriginCity = this.aviaModule.city_from;\n            this.selectedDestinationCity = this.aviaModule.city_to;\n            this.depart_date = this.aviaModule.depart_date;\n            if(this.aviaModule.return_date!=null){\n                this.return_date =this.aviaModule.return_date;\n            }\n\n            this.avia_class = this.aviaModule.avia_class;\n            // this.ru = ru;\n        },\n        computed: {\n            aviaModule () {\n                return this.$store.getters.aviaModule;\n            },\n\n            avia_steps () {\n                return this.$store.getters.avia_steps;\n            },\n\n            aviaCities() {\n                return this.$store.getters.aviaCities;\n            },\n\n            isAviaCitiesLoading() {\n                return this.$store.getters.isAviaCitiesLoading;\n            },\n        },\n        methods: {\n            nextStep() {\n                this.$store.dispatch('changeAviaSteps', {key:'step2', value:true});\n                this.$router.push('/avia-module/order')\n            },\n            getAviaCities (term) {\n                if( term.length > 1) {\n                    this.$store.dispatch('getAviaCities', term).then(() => {\n                        this.options = this.aviaCities;\n                    })\n                }\n            },\n            changeCityFrom () {\n                this.$store.dispatch('changeCityFrom', this.selectedOriginCity)\n            },\n            changeCityTo () {\n                this.$store.dispatch('changeCityTo', this.selectedDestinationCity)\n            },\n            toggleAviaCities() {\n                var tempCity = this.selectedDestinationCity;\n                this.selectedDestinationCity = this.selectedOriginCity;\n                this.selectedOriginCity = tempCity;\n                this.$store.dispatch('changeCityFrom', this.selectedOriginCity)\n                this.$store.dispatch('changeCityTo', this.selectedDestinationCity)\n            },\n            changeDepartDate () {\n                this.$store.dispatch('changeDepartDate', this.depart_date)\n                if(this.return_date != null) {\n                    var date1 = new Date(this.depart_date);\n                    var date2 = new Date(this.return_date);\n\n                    if(date1 > date2)\n                    {\n                        this.return_date = this.depart_date;\n                        this.$store.dispatch('changeReturnDate', this.return_date)\n                        this.disabledDates2.to = this.depart_date;\n                    }\n                }\n            },\n            changeReturnDate () {\n                this.$store.dispatch('changeReturnDate', this.return_date)\n            },\n            incrementAdults() {\n                this.$store.dispatch(\"incAviaAdults\")\n                this.adults = this.aviaModule.adults\n            },\n            decrementAdults() {\n                this.$store.dispatch(\"decAviaAdults\")\n                this.adults = this.aviaModule.adults\n            },\n            incrementChildrenUnder12() {\n                this.$store.dispatch(\"incChildrenUnder12\")\n                this.children_under_12 = this.aviaModule.children_under_12\n            },\n            decrementChildrenUnder12() {\n                this.$store.dispatch(\"decChildrenUnder12\")\n                this.children_under_12 = this.aviaModule.children_under_12\n            },\n            incrementChildrenUnder2() {\n                this.$store.dispatch(\"incChildrenUnder2\")\n                this.children_under_2 = this.aviaModule.children_under_2\n            },\n            decrementChildrenUnder2() {\n                this.$store.dispatch(\"decChildrenUnder2\")\n                this.children_under_2 = this.aviaModule.children_under_2\n            },\n            changeChildrenUnder12() {\n                this.$store.dispatch(\"changeChildrenUnder12\", this.children_under_12)\n                this.children_under_12 = this.aviaModule.children_under_12\n            },\n            changeChildrenUnder2() {\n                this.$store.dispatch(\"changeChildrenUnder2\", this.children_under_2)\n                this.children_under_2 = this.aviaModule.children_under_2\n            },\n            changeAdults() {\n                this.$store.dispatch(\"changeAviaAdults\", this.adults)\n                this.adults = this.aviaModule.adults\n            },\n            changeClass() {\n                this.$store.dispatch(\"changeClass\",